html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.logo {
  position: fixed;
  top: 30px;
  left: 30px;
  background: #fff;
  display: block;
  z-index: 1000;
  width: 70px;
  height: 70px;
  text-align: center;
  line-height: 70px;
  font-size: 40px;
  text-decoration: none;
}

.logo span {
  font-size: 10px;
  display: block;
  position: absolute;
  bottom: 5px;
  left: 0;
  right: 0;
  text-align: center;
  text-align: center;
  line-height: 1;
}

.is-cloned {
  position: absolute !important;
  top: 0;
}

.items {
  height: 100vh;
  width: 100vw;
  overflow: auto;
  white-space: nowrap;
  font-size: 0;
}

.item {
  display: inline-block;
  width: 33.33%;
  height: 100vh;
  font-size: 20px;
  position: relative;
  overflow: hidden;
}

.item__title {
  color: #fff;
  font-weight: bold;
  font-family: Arial,sans-serif;
  font-size: 40px;
  position: absolute;
  bottom: 100px;
  left: 20px;
  transition: transform .3s ease-in-out;
}

.item__bg {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  transform: translate(-30%, 0);
}

.item:hover .item__title {
  transform: scale(1.1);
}

.content {
  padding: 50px;
  font-size: 40px;
  line-height: 1.5;
}

.content p {
  margin-bottom: 50px;
}

.single {
  height: 100vh;
  font-size: 20px;
  position: relative;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

.single__title {
  color: #fff;
  font-weight: bold;
  font-family: Arial,sans-serif;
  font-size: 40px;
  position: absolute;
  bottom: 100px;
  left: 50px;
  transform: translate(0, 200px);
}
